import regions from './region.js'
export default {
	data() {
		return {
			leftList: [],
			rightList: [],
			currentIndex: 0
		};
	},
	created() {
		let arrs = regions[0].children
		//生成左边和右边的菜单栏数据
		arrs.map((item, index) => {
			this.leftList.push(
				{
					lid: index, 
					value: item.value.replace(/(省|自治区|特别行政区|回族|壮族|维吾尔)/g, ''), 
					children: item.children,
				}
			)
		})
		
	},
	computed: {
		nowRightList() {
			this.rightList = []
			this.leftList[this.currentIndex].children.map((el, i) => {
				if(el.value.indexOf('族') === -1) {
					this.rightList.push({rid: i, value: el.value.replace(/(市|自治州|特别行政区|地区|盟|区)/g, '')})
				}
			})
			// console.log(this.rightList)
			return this.rightList
		}
	},
	methods: {
		selectCity(e) {
			// console.log(e)
			this.currentIndex = e.target.dataset.index
			console.log(this.currentIndex)
		},
		comfirmCity(e) {
			// console.log(e.target.dataset.city)
			this.$store.dispatch('setHopeCity', e.target.dataset.city)
			uni.navigateBack({
				delta: 1
			})
		}
	}
}
